html{
  scrollbar-width: none;
}

body{
  background-color: rgb(37, 38, 70);
  padding: 7% 10%;
}

p{
  font-size:1.5rem;
}

#progressBarContainer{
  position: fixed;
  background-color:transparent;
  width:100%;
  top: 0;
  left: 0;
  height: 10px;
  z-index: 1000;
}

#progressBar{
  background: linear-gradient(to left, red, blue);
  transform-origin: top left;
  transform: scale(0,0);
  opacity: 0;
  height:10px;
}